<script>
	import Map from './Map.svelte';
	import Panel from './Panel.svelte';
	import { getPlaces, getBoundary, rndPlace, shuffle } from './utils.js';
	
	let map;
	let mapstyle = './data/mapstyle.json';
	let places;
	
	let game = {
		started: false,
		turn: 0,
		score: 0,
		place: null,
		places: null,
		message: null
	}
	
	// New turn. Randomly select a place + get its polygon
	function nextTurn() {
		if (!game.started) { game.started = true };
		let id = 'boundary';
		
		// Get a random place (right answer)
		let place = rndPlace(places);
		game.place = place;
		
		// Create an array of possible answers
		game.places = [];
		game.places.push(place);
		while (game.places.length < 3) {
			let place = rndPlace(places);
			if (!game.places.includes(place)) {
				game.places.push(place);
			}
		}
		game.places = shuffle(game.places);
		
		// Get geographic boundary of selected place
		let boundary;
		getBoundary(place.code)
		.then(result => {
			// Remove previous boundary from the map
			if (map.getLayer(id)) {
				map.removeLayer(id);
				map.removeSource(id);
			}
			
			// Add new boundary to the map
			map.addSource(id, {
				'type': 'geojson',
				'data': result.geometry
			});
			map.addLayer({
				'id': id,
				'type': 'line',
				'source': id,
				'layout': {},
				'paint': {
					'line-color': '#fff',
					'line-width': 2
				}
			});
			
			// Fit map to boundary
			map.fitBounds(result.bounds, { padding: 20, animate: false });
		});
	}
	
	// Check if chosen place is correct
	function checkPlace(code) {
		if (code == game.place.code) {
			game.score += 1;
			game.message = `You got it!`;
		} else {
			game.message = `Nope, the answer was ${game.place.name}`;
		}
		game.turn += 1;
		game.place = null;
		game.places = null;
	}
	
	// Get geography codes and names from API (initiation of app)
	getPlaces().then(result => { places = result; })
	
</script>

<style>
	:global(body) {
		margin: 0;
		padding: 0;
	}
	main {
		padding: 20px;
	}
	h1 {
		margin-top: 10px;
	}
	.block {
		display: block;
		width: 100%;
	}
</style>

<Panel>
	<main>
		<h1>Can you guess the local authority?</h1>
		<h3>Score {game.score} / {game.turn} {#if game.turn > 0}({Math.round((game.score / game.turn) * 100)}%){/if}</h3>
		{#if !game.started && places}
	  	<button on:click={nextTurn}>Let's get started!</button>
		{:else if game.places}
		{#each game.places as place}
	  	<button class="block" on:click={checkPlace(place.code)}>{place.name}</button>
		{/each}
		{:else if game.message}
			<h3>{game.message}</h3>
			<button on:click={nextTurn}>Show me another!</button>
		{/if}
	</main>
</Panel>

<Map style={mapstyle} bind:map={map} />